body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.image-space {
    border: 5px darkgray dotted;
    margin: 30px;
    flex: 2 1;
}
.info-box {
    text-align: left;
    border: 5px darkgray dotted;
    margin: 30px;
    height: 100%;
    padding: 5px;
    flex: 2 1;
}

.info-box h3 {
    margin: 0px;
}

.info-stats {
    font-size: 10px;
}

.color-table {
    width: 200px;
    height: 200px;
    border-collapse: collapse;
}

.tooltip {
    visibility: hidden;
    position: absolute;
    background-color: gray;
    color: white;
    left: 25px;
    width: 100px;
    font-size: 10pt;
    line-height: 3pt;
    border: 3px dotted black;
}

.color-table td {
    position: relative;
}

.color-table td:hover .tooltip {
    visibility: visible;
    z-index: 1;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.gif-flexbox {
  display: flex;
  flex-direction: row;
}
